* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@media screen and (min-width: 500px) {
  body {
    background-image: url("ruvim-noga-o0R4iqk1eRQ-unsplash.ae198abc.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }

  main {
    flex-direction: column;
    display: flex;
  }

  main h1 {
    color: #fcffe7;
    margin-bottom: 10px;
    font-family: Open sans;
    font-size: 5rem;
    display: flex;
  }

  main h1 div {
    color: #75a5db;
    transition: all 1.5s;
    animation: rubberband 3.2s ease-out 2;
  }

  main .mypicture, main .mypicture img {
    display: none;
  }

  .intro {
    min-width: 100%;
  }

  @keyframes rubberband {
    0% {
      transform: scaleX(1.3)scaleY(1);
    }

    40% {
      transform: scaleX(1.12)scaleY(.75);
    }

    55% {
      transform: scaleX(.85)scaleY(1);
    }

    65% {
      transform: scaleX(1.09)scaleY(.85);
    }

    75% {
      transform: scaleX(1)scaleY(1);
    }

    90% {
      transform: scaleX(1.05)scaleY(.95);
    }

    100% {
      transform: scaleX(1.2)scaleY(1);
    }
  }

  .social, .links, .infobar {
    display: none;
  }

  .navbar {
    font-size: 2.4rem;
    position: fixed;
    top: 2%;
    right: 7%;
  }

  .navbar a {
    color: #fcffe7;
  }

  p {
    width: 100%;
    color: #fcffe7;
    font-size: 2rem;
  }

  b {
    color: #c55b89;
  }

  #description {
    min-width: 100%;
  }
}

@media screen and (min-width: 1020px) {
  body {
    background-image: url("ruvim-noga-o0R4iqk1eRQ-unsplash.ae198abc.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }

  .overlay {
    background-color: #0000006e;
    padding-top: 40px;
    padding-left: 20px;
  }

  .navbar {
    display: none;
  }

  .sidebar {
    height: 110vh;
    width: 140px;
    background-color: #000;
    border-radius: 5px;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    position: fixed;
    top: 0;
    box-shadow: 0 15px 25px #75a5db;
  }

  .sidebar .infobar {
    display: block;
  }

  .sidebar .infobar .Me {
    color: #75a5db;
    margin-left: 5px;
    font-family: Monoton;
    font-size: 7rem;
    animation: rubberband 3s ease-out infinite;
  }

  .sidebar .infobar .me {
    color: #fcffe7;
    color: #75a5db;
    font-family: Monoton;
  }

  .sidebar .infobar .me a {
    margin-left: 10px;
    font-family: Monoton;
    font-size: 2rem;
  }

  .sidebar .infobar .wd {
    color: #7560c0;
    margin-left: 5px;
    font-family: Open Sans, sans-serif;
    font-size: 1.1rem;
  }

  .sidebar li {
    list-style: none;
  }

  .sidebar a {
    color: #75a5db;
    margin-bottom: 20px;
    font-family: Open Sans, sans-serif;
    font-size: 1.5rem;
    text-decoration: none;
  }

  .sidebar a:hover {
    font-size: 2rem;
    transition: all .8s;
  }

  .sidebar .social {
    margin-bottom: 150px;
    margin-left: 15px;
    display: flex;
  }

  .sidebar .social li {
    margin: 5px;
  }

  .sidebar .socialhover {
    font-size: 2.5rem;
    transition: all .8s;
  }

  .links {
    display: block;
  }

  .links li {
    width: 100%;
    border-bottom: 3px solid #7560c0;
    margin-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    list-style: none;
  }
}

@keyframes rubberband {
  0% {
    transform: scaleX(1.3)scaleY(1)rotate(360deg);
  }

  40% {
    transform: scaleX(1.12)scaleY(.75);
  }

  55% {
    transform: scaleX(.85)scaleY(1);
  }

  65% {
    transform: scaleX(1.09)scaleY(.85);
  }

  75% {
    transform: scaleX(1)scaleY(1);
  }

  90% {
    transform: scaleX(1.05)scaleY(.95);
  }

  100% {
    transform: scaleX(1.2)scaleY(1);
  }
}

